import React, { useMemo, useEffect, useContext, useRef, useState } from 'react'
import io from 'socket.io-client';
import { MainContext } from '../Context/MainContext';
import { Device } from 'mediasoup-client';
import '../style/room.css';
import Logo from '../images/logo.jpg'
import IconButton from '../Components/IconButton';
import Modal from 'react-modal';
import Button from '../Components/Button';

let producerTransport = null;
let consumerTransport = null;
let consumers = new Map()
let producers = new Map();
let producer = null
let producerLabel = new Map()


function Room() {
  const socket = useMemo(() => io("https://13.80.170.208:3016"), []);
  const { roomData } = useContext(MainContext);
  const devicesRef = useRef();
  const videoRef = useRef(null);
  const [isVideoHide, setIsVideoHide] = useState(false);
  const [isAudioHide, setIsAudioHide] = useState(false);
  const [isScreenShare, setIsScreenShare] = useState(false);
  const screenShareStream = useRef(null);

  // const 
  const [remoteVideos, setRemoteVideos] = useState([]);

  useEffect(() => {
    initSFU();
    setIsOpen(true);
  }, [])
  const initSFU = () => {
    createRoom().then(async (success) => {
      await joinRoom();
    }).catch((error) => { console.log(error) });


    socket.on(
      'newProducers',
      async function (data) {
        console.log('New producers', data)
        for (let { producer_id } of data) {
          await getConsumeStream(producer_id);
        }
      }
    )

    socket.on(
      'consumerClosed',
      function ({ consumer_id }) {
        console.log('Closing consumer:', consumer_id)
        removeConsumer(consumer_id)
      }
    )
    socket.on(
      'disconnect',
      function () {
        exit(true)
      }
    )
  }
  const createRoom = async () => {
    return new Promise((resolve, reject) => {
      if (roomData === null) {
        reject("error room not created");
      } else {
        socket.emit("createRoom", { room_id: roomData.roomId }, (data) => {
          if (data.error) {
            reject("error From server room not created")
          } else {
            resolve("success");
          }
        });
      }
    })
  }
  const joinRoom = async () => {
    if (roomData === null) {
      console.log("room Data not null");
    } else {
      socket.emit("join", { room_id: roomData.roomId, name: roomData.name }, async (e) => {
        console.log('Joined to room', e)
        socket.emit('getRouterRtpCapabilities', {}, async (data) => {
          let device = await loadDevice(data)
          devicesRef.current = device;
          await initTransports(device)
        })
      });
    }
  }
  const loadDevice = async (routerRtpCapabilities) => {
    let device
    try {
      device = new Device()
    } catch (error) {
      if (error.name === 'UnsupportedError') {
        console.error('Browser not supported')
        alert('Browser not supported')
      }
      console.error(error)
    }
    await device.load({
      routerRtpCapabilities
    })
    return device
  }
  const initTransports = async (device) => {

    socket.emit('createWebRtcTransport', {
      forceTcp: false,
      rtpCapabilities: device.rtpCapabilities
    }, (data) => {
      if (data.error) {
        console.error(data.error)
        return
      }
      producerTransport = device.createSendTransport(data)
      producerTransport.on(
        'connect',
        async function ({ dtlsParameters }, callback, errback) {
          socket
            .emit('connectTransport', {
              dtlsParameters,
              transport_id: data.id
            }, (data) => {
              if (data.error) {
                errback();
              } else {
                callback();
              }
            })
        }
      )
      producerTransport.on(
        'produce',
        async function ({ kind, rtpParameters }, callback, errback) {
          try {
            socket.emit('produce', {
              producerTransportId: producerTransport.id,
              kind,
              rtpParameters
            }, ({ producer_id }) => {
              callback({
                id: producer_id
              })
            })

          } catch (err) {
            errback(err)
          }
        }
      )

    });

    // init consumerTransport

    {
      socket.emit('createWebRtcTransport', {
        forceTcp: false
      }, (data) => {
        if (data.error) {
          console.error(data.error)
          return
        }
        consumerTransport = device.createRecvTransport(data)
        socket.emit('getProducers')

        consumerTransport.on(
          'connect',
          function ({ dtlsParameters }, callback, errback) {
            socket
              .emit('connectTransport', {
                transport_id: consumerTransport.id,
                dtlsParameters
              }, (data) => {
                if (data.error) {
                  errback(data.error);
                } else {
                  callback();
                }
              })
          }
        )
      })

      // if(consumerTransport===null){
      //   console.log("ddd");
      // }else{
      // }
    }


  }
  const mediaType = {
    audio: 'audioType',
    video: 'videoType',
    screen: 'screenType'
  }


  const screenShare = async (type) => {
    if (!devicesRef.current.canProduce('video')) {
      console.error('Cannot produce video')
      return
    }
    if (producerLabel.has(type)) {
      console.log('Producer already exists for this type ' + type)
      return
    }
    try {
      let stream = await navigator.mediaDevices.getDisplayMedia();
      const track = stream.getVideoTracks()[0]
      const params = {
        track
      }
      params.encodings = [
        // {
        //   rid: 'r0',
        //   maxBitrate: 100000,
        //   scalabilityMode: 'S1T3'
        // },
        // {
        //   rid: 'r1',
        //   maxBitrate: 300000,
        //   scalabilityMode: 'S1T3'
        // },
        // {
        //   rid: 'r2',
        //   maxBitrate: 900000,
        //   scalabilityMode: 'S1T3'
        // }
        { rid: 'r0', scalabilityMode: 'S1T3' },
        { rid: 'r1', scalabilityMode: 'S1T3' },
        { rid: 'r2', scalabilityMode: 'S1T3' },
        { rid: 'r3', scalabilityMode: 'S1T3' }
      ]
      params.codecOptions = {
        videoGoogleStartBitrate: 1000
      }
      producer = await producerTransport.produce(params)
      screenShareStream.current.srcObject = stream;
      producers.set(producer.id, producer)
      producer.on('trackended', () => {
        closeProducer(type)
      })
      producer.on('transportclose', () => {
        console.log('Producer transport close')
        screenShareStream.current.srcObject.getTracks().forEach(function (track) {
          track.stop()
        })
        producers.delete(producer.id)
      })
      producer.on('close', () => {
        console.log('Closing producer')
        // if (!audio) {
        screenShareStream.current.srcObject.getTracks().forEach(function (track) {
          track.stop()
        })
        // }
        producers.delete(producer.id)
      })
      producerLabel.set(type, producer.id)
    } catch (error) {
      console.log('Produce error:', error)
    }
  }


  const produce = async (type, deviceId = null) => {
    let mediaConstraints = {
      audio: true,
      video: {
        width: {
          min: 640,
          ideal: 1920
        },
        height: {
          min: 400,
          ideal: 1080
        },
      }
    };
    if (!devicesRef.current.canProduce('video')) {
      console.error('Cannot produce video')
      return
    }
    if (producerLabel.has(type)) {
      console.log('Producer already exists for this type ' + type)
      return
    }
    try {
      let stream = await navigator.mediaDevices.getUserMedia(mediaConstraints)
      const track = stream.getVideoTracks()[0]
      const params = {
        track
      }
      params.encodings = [
        {
          rid: 'r0',
          maxBitrate: 100000,
          scalabilityMode: 'S1T3'

        },
        {
          rid: 'r1',
          maxBitrate: 300000,
          scalabilityMode: 'S1T3'
        },
        {
          rid: 'r2',
          maxBitrate: 900000,
          scalabilityMode: 'S1T3'
        }
      ]
      params.codecOptions = {
        videoGoogleStartBitrate: 1000
      }
      producer = await producerTransport.produce(params)
      videoRef.current.srcObject = stream;
      producers.set(producer.id, producer)
      producer.on('trackended', () => {
        closeProducer(type)
      })
      producer.on('transportclose', () => {
        console.log('Producer transport close')
        // if (!audio) {
        videoRef.current.srcObject.getTracks().forEach(function (track) {
          track.stop()
        })
        // }
        producers.delete(producer.id)
      })
      producer.on('close', () => {
        console.log('Closing producer')
        // if (!audio) {
        videoRef.current.srcObject.getTracks().forEach(function (track) {
          track.stop()
        })
        // }
        producers.delete(producer.id)
      })
      producerLabel.set(type, producer.id)
    } catch (err) {
      console.log('Produce error:', err)
    }
  }
  const getConsumeStream = async (producerId) => {
    const { rtpCapabilities } = devicesRef.current;
    // console.log( consumerTransport.id)
    let consumer, kind1;
    socket.emit('consume', {
      rtpCapabilities,
      consumerTransportId: consumerTransport.id, // might be
      producerId
    }, async (data) => {
      const { id, kind, rtpParameters } = data
      kind1 = kind;
      console.log(kind, rtpParameters);

      let codecOptions = {}
      consumer = await consumerTransport.consume({
        id,
        producerId,
        kind,
        rtpParameters,
        codecOptions
      })
      // console.log(consumer.track);
      const stream = new MediaStream()
      stream.addTrack(consumer.track)
      consumers.set(consumer.id, consumer)
      const remoteVideo = {
        id: consumer.id,
        stream: stream
      };
      setRemoteVideos(e => [...e, remoteVideo]);
      const elem = document.createElement('div')
      elem.classList.add("remoteVideoBox")
      const video = document.createElement("video");
      video.srcObject = stream;

      video.id = consumer.id
      video.play();
      video.playsinline =true
      video.autoplay = true
      video.className = 'vid'
      elem.append(video);
      document.getElementById('remoteVideos').appendChild(elem);


      consumer.on(
        'trackended',
        function () {
          removeConsumer(consumer.id)
        }
      )

      consumer.on(
        'transportclose',
        function () {
          this.removeConsumer(consumer.id)
        }
      )
    })

  }
  const closeProducer = (type) => {
    if (!producerLabel.has(type)) {
      console.log('There is no producer for this type ' + type)
      return
    }

    let producer_id = producerLabel.get(type)
    console.log('Close producer', producer_id)

    socket.emit('producerClosed', {
      producer_id
    })

    producers.get(producer_id).close()
    producers.delete(producer_id)
    // this.producerLabel.delete(type)

    if (type !== mediaType.audio) {
      let elem = document.getElementById(producer_id)
      elem.srcObject.getTracks().forEach(function (track) {
        track.stop()
      })
      elem.parentNode.removeChild(elem)
    }
  }
  const removeConsumer = (consumer_id) => {
    let elem = document.getElementById(consumer_id)
    elem.srcObject.getTracks().forEach(function (track) {
      track.stop()
    })
    elem.parentNode.removeChild(elem)

    consumers.delete(consumer_id)
  }
  const exit = (offline = false) => {
    let clean = function () {
      consumerTransport.close()
      producerTransport.close()
      socket.off('disconnect')
      socket.off('newProducers')
      socket.off('consumerClosed')
    }

    if (!offline) {
      socket
        .emit('exitRoom', {}, (data) => {
          if (data.error) {
            console.log(data.error);
          } else {
            clean();

          }
        })

    } else {
      clean()
    }
  }
  const customStyles = {
    content: {
      top: '50%',
      left: '40%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-60%',
      transform: 'translate(-50%, -50%)',
      border: "none",
      backgroundColor: "#fff",
      boxShadow: ' rgba(100, 100, 111, 0.2) 0px 7px 29px 0px',
      borderRadius: "10px",
    },
  };

  Modal.setAppElement('#root');


  const [modalIsOpen, setIsOpen] = React.useState(false);

  function createObjectURL(object) {
    return (window.URL) ? window.URL.createObjectURL(object) : window.webkitURL.createObjectURL(object);
  }


  function closeModal() {
    setIsOpen(false);
  }

  const onToggleVideo = () => {
    setIsVideoHide(!isVideoHide);
    if (isVideoHide) {
      videoRef.current.srcObject.getVideoTracks()[0].enabled = true;
    } else {
      videoRef.current.srcObject.getVideoTracks()[0].enabled = false;
    }
  }
  const onToggleAudio = () => {
    setIsAudioHide(!isAudioHide);
    if (isAudioHide) {
      videoRef.current.srcObject.getAudioTracks()[0].enabled = true;
    } else {
      videoRef.current.srcObject.getAudioTracks()[0].enabled = false;
    }
  }


  function playVideo(element, stream) {
    if (element.srcObject) {
      console.warn('element ALREADY playing, so ignore');
      return;
    }
    element.srcObject = stream;
    element.volume = 0;
    return element.play();
  }

  return (
    <div className='container'>
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={() => { }}
        // onRequestClose={closeModal}

        style={customStyles}
        contentLabel="Example Modal"
      >

        <IconButton onPress={() => { produce(mediaType.video, "video") }} iconName={"video-slash"} />
        {/* <IconButton onPress={() => { produce(mediaType.audio, "audio") }} iconName={"microphone-slash"} /> */}

        <Button onPress={closeModal} title={"close"} />
      </Modal>
      <div className='left-section'>
        <div className='header-nav'>
          <img src={Logo} alt='logo' />
          <i className='fa fa-users' />
        </div>
        <div className="body">
          <div className='localVideo' id='localVideo'>
            <video ref={videoRef} className="vid" autoPlay />
            <video ref={screenShareStream} className="vid" autoPlay />

          </div>
          <div className='remoteVideos' id='remoteVideos'>
            {/* <div className='remoteVideoBox'></div> */}
            {/* {
              remoteVideos.map((v, k) => {
                return <div key={k} className='remoteVideoBox'>
                  <MemoizedCreateRemoteVideos
                    key={k}
                    peer={v}
                    playVideo={playVideo}
                  />
                </div>
              })
            } */}
          </div>
        </div>
        <div className="footer">
          <IconButton iconName={isVideoHide ? "video-slash" : "video"} onPress={onToggleVideo} />
          {/* microphone-slash */}
          <IconButton iconName={isAudioHide ? "microphone-slash" : "microphone"} onPress={onToggleAudio} />
          <IconButton iconName={"display"} onPress={() => {
            screenShare(mediaType.screen);
          }} color="blue" />
          <IconButton iconName={"comment"} />
          <IconButton iconName={"rectangle-xmark"} color="red" onPress={exit} />

        </div>
      </div>
      <div className='right-section'></div>


      {/* <h1>Room</h1>
     
      <button onClick={()=>{produce(mediaType.video,"video")}}>openVideo</button>
      <button onClick={()=>{produce(mediaType.audio,"audio")}}>openAudio</button> */}

    </div>

  )


}

export default Room


const CreateRemoteVideos = (props) => {
  const remoteVideo = React.useRef();
  React.useEffect(() => {

    console.log('CreateRemoteVideos');
    console.log(props);
    props
      .playVideo(remoteVideo.current, props.peer.stream)
      ?.then(() => {
        remoteVideo.current.volume = 1;
        console.log('remoteVideo.current');
        console.log(remoteVideo.current);
      })
      .catch((err) => {
        console.error('media ERROR:', err);
      });
  }, []);
  return (
    <video
      ref={remoteVideo}
      controls
      autoPlay
      style={{
        width: '240px',
        height: '180px',
        border: '1px solid black',
      }}
    ></video>
  );
};
export const MemoizedCreateRemoteVideos = React.memo(CreateRemoteVideos);