import React from 'react'

function IconButton({iconName,color="black",onPress}) {
    return (
        <button 
        onClick={onPress}
        style={{
            width: '3rem'
            , height: '3rem',
            borderRadius: "50%",
            border: "none",
            marginInline: "1rem",
            backgroundColor: "#fff",
            boxShadow: ' rgba(100, 100, 111, 0.2) 0px 7px 29px 0px',
            color:color,
            fontSize:18,
            cursor:"pointer",
            textAlign:"center"

        }}>
            <i className={`fa fa-${iconName}`} />
            
        </button>
    )
}

export default IconButton