import React from 'react'



function Button({
    title,onPress,bgColor,
    textColor
}) {

    const btnStyle ={
        width:"82%",
        height:"6vh",
        border:"none",
        marginTop:10,
        borderRadius:5,
        cursor: "pointer",
        backgroundColor:bgColor,
        color:textColor,
        fontWeight:700,
        fontSize:15
    };


  return (
    <button onClick={onPress} style={btnStyle}>{title}</button>
  )
}

export default Button